<template>
    <div class="homeBox">
        <el-container>
            <el-header>
                <div>
                    <el-menu
                            :default-active="activeIndex2"
                            class="el-menu-demo"
                            mode="horizontal"
                            @select="handleSelect"
                            background-color="#545c64"
                            text-color="#fff"
                            active-text-color="#ffd04b">
                        <el-menu-item index="1">处理中心</el-menu-item>
                        <el-submenu index="2">
                            <!-- 更新：到我的小组界面 -->
                            <template slot="title">个人中心</template>
                            <el-menu-item index="2-1" @click="cardClick('/groups')">热门小组</el-menu-item>
                            <el-menu-item index="2-2" @click="cardClick('/group')">我的小组</el-menu-item>
                            <!-- 更新完毕 -->
                        </el-submenu>
                        <el-menu-item index="3"><a href="https://www.ele.me" target="_blank" style="text-decoration: none;">订单管理</a></el-menu-item>
                        <el-menu-item index="3-5" @click="cardClick('/h')">话题页</el-menu-item>
                        <el-menu-item index="4" style="float: right;" @click="cardClick('/regi')">注册</el-menu-item>
                        <el-menu-item index="5" style="float: right;" @click="cardClick('/login')">登录</el-menu-item>
                    </el-menu>
                </div>
            </el-header>
            <el-container style="height:100%;">
                <el-aside width="200px">
                    <div>
                        <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" background-color="#434c58" text-color="#fff" active-text-color="#ffd04b">
                            <el-submenu index="1">
                                <template slot="title">
                                    <i class="el-icon-location"></i>
                                    <span slot="title">导航一</span>
                                </template>
                                <el-menu-item-group>
                                    <span slot="title">分组一</span>
                                    <el-menu-item index="1-1">选项1</el-menu-item>
                                    <el-menu-item index="1-2">选项2</el-menu-item>
                                </el-menu-item-group>
                                <el-menu-item-group title="分组2">
                                    <el-menu-item index="1-3">选项3</el-menu-item>
                                </el-menu-item-group>
                                <el-submenu index="1-4">
                                    <span slot="title">选项4</span>
                                    <el-menu-item index="1-4-1">选项1</el-menu-item>
                                </el-submenu>
                            </el-submenu>
                            <el-menu-item index="2" @click="cardClick('/book')">
                                <i class="el-icon-menu"></i>
                                <span slot="title">图书页</span>
                            </el-menu-item>
                            <el-menu-item index="3" @click="cardClick('/movie')">
                                <i class="el-icon-setting"></i>
                                <span slot="title">影视页</span>
                            </el-menu-item>
                        </el-menu>
                    </div>
                </el-aside>

                <el-container>
                    <el-main>

                        <div style="padding-bottom: 20px;padding-left: 30%">
                            <el-row>
                                <el-col :span="10" class="center">
                                    <el-input
                                            v-model="search"
                                            @focus="focus"
                                            @blur="blur"
                                            @keyup.enter.native="searchHandler"
                                            placeholder="这搜索就礼璞"
                                    >
                                        <el-button slot="append" icon="el-icon-search" id="search" @click="searchHandler"></el-button>
                                    </el-input>
                                    <!---设置z-index优先级,防止被走马灯效果遮挡-->
                                    <el-card
                                            @mouseenter="enterSearchBoxHanlder"
                                            v-if="isSearch"
                                            class="box-card"
                                            id="search-box"
                                            style="position:absolute;z-index:15;width:40%"
                                    >
                                        <dl v-if="isHistorySearch">
                                            <dt class="search-title" v-show="history">历史搜索</dt>
                                            <dt class="remove-history" v-show="history" @click="removeAllHistory">
                                                <i class="el-icon-delete"></i>清空历史记录
                                            </dt>
                                            <el-tag
                                                    v-for="search in historySearchList"
                                                    :key="search.id"
                                                    closable
                                                    :type="search.type"
                                                    @close="closeHandler(search)"
                                                    style="margin-right:5px; margin-bottom:5px;"
                                            >{{search.name}}</el-tag>
                                            <dt class="search-title">热门搜索</dt>
                                            <dd v-for="search in hotSearchList" :key="search.id">{{search}}</dd>
                                        </dl>
                                        <dl v-if="isSearchList">
                                            <dd v-for="search in searchList" :key="search.id">{{search}}</dd>
                                        </dl>
                                    </el-card>
                                </el-col>
                            </el-row>
                        </div>

                        <div>
                            <template>
                                <el-carousel :interval="4000" type="card" height="200px">
                                    <el-carousel-item v-for="item in 6" :key="item">
                                        <h3>{{ item }}</h3>
                                    </el-carousel-item>
                                </el-carousel>
                            </template>
                        </div>
                            <plist v-bind:list="allsite"></plist>

                    </el-main>
                    <el-footer>
                        <div>

                        </div>
                    </el-footer>
                </el-container>

            </el-container>
        </el-container>
    </div>
</template>


<script>
    import RandomUtil from "@/utils/randomUtil";
    import Store from "@/utils/store";
    import plist from '@/components/plist.vue'

    export default {
        components:{
            plist
        },
        name:'Page',
        data() {
            return {
                allsite:[

                    {url:'http://r.photo.store.qq.com/psc?/V12aZVkM2Rfh0C/YWvjNfAyIVey1fwA2tD8GJL7asGAEXWxoRoNEwG30NpryyI.KE2DTFY7mgH9aErnQaVF2rR9jgnJJT2.PdwkpWZdQjVxRGm6rEC0TNpPnPQ!/r',name:'白夜行',time:'[日] 东野圭吾',path:'401'},
                    {url:'http://r.photo.store.qq.com/psc?/V12aZVkM2Rfh0C/YWvjNfAyIVey1fwA2tD8GGvWC9YGJELCbnPUpUdljk7gx4gHn8fsBYl1pDb2BPyx8pZ8swVp9iwvDGLwT6oINSZz*NhAGvtnWBkriPIRFoQ!/r',name:'一拳超人',time:'ONE / 村田雄介',path:'402'},

                    {url:require('@/assets/p.webp'),name:'海兽之子',time:'动画 / 奇幻 / 冒险',path:'500'},
                    {url:require('@/assets/heat8.jpg'),name:'小丑 Joker',time:'剧情 / 惊悚 / 犯罪',path:'501'},
                    {url:'http://r.photo.store.qq.com/psc?/V12aZVkM2Rfh0C/YWvjNfAyIVey1fwA2tD8GBgD7dpyaM6Z59J.0ZRZFGZBInxIIgZC2jM5B3UotVTcUgYodHmTHg07A.0yh5hc*4rEbYEYuF6zwOTsSUa.dbE!/r',name:'利刃出鞘',time:'剧情/喜剧/悬疑/犯罪',path:'502'},
                    {url:'http://r.photo.store.qq.com/psc?/V12aZVkM2Rfh0C/YWvjNfAyIVey1fwA2tD8GB9.3NIHzSe8IH.9zey8Gst49Gagg2yjsc8x6Ldd*aOmssURnkCibNSFm8sgXKQc5xPgH2c7lTksB*2EDPS0Vv0!/r',name:'春潮',time:'剧情 / 家庭',path:'503'},
                    {url:'http://r.photo.store.qq.com/psc?/V12aZVkM2Rfh0C/YWvjNfAyIVey1fwA2tD8GP3hoKUPe16Ioh004E3PZUsgYPwoh1B9ZGZ5kcNxKG9tv.ehY8syrXSjLz8oGfFFlpXb.Gw7c.LsDpcqlkpkWUc!/r',name:'小谢尔顿 第一季',time:'喜剧',path:'504'},
                    {url:'http://r.photo.store.qq.com/psc?/V143D3j445iBwL/ubiEST8aMMlZjEEUGVmWIleHaC27qlqXMUn0Jwap39LwmhQwrsLCUWR.oOAq7QE4OqRPMHmFmkY8FEmNDQ3vhZCFfGWZEBxMbNFCewTz1n0!/r',name:'我不是药神',time:'剧情 / 喜剧',path:'505'},
                    {url:'http://r.photo.store.qq.com/psc?/V143D3j445iBwL/ubiEST8aMMlZjEEUGVmWIh2hlfEf6HoO.yPPwWMmT5bjDqEpBa6MwlJLrFAzWp1Lm8ZISROIToimNf7fm0maqzVrACEOJ8QSpdQdo5pdgHQ!/r',name:'寻梦环游记 Coco',time:'喜剧 / 动画 / 音乐 / 奇幻',path:'506'},
                    {url:'http://r.photo.store.qq.com/psc?/V143D3j445iBwL/ubiEST8aMMlZjEEUGVmWIjzibbsG1jfAmSW3tevHG8Um0XcejElFTQYOZ.M54fE5AQ5Bba1XnbS4*MPOPvmnHYwbJdMGfL9v5s.t9q5OsMA!/r',name:'头号玩家',time:'动作 / 科幻 / 冒险',path:'507'},
                    {url:'http://r.photo.store.qq.com/psc?/V143D3j445iBwL/ubiEST8aMMlZjEEUGVmWInIKeiRrq334bRnYCHal0EKNO1DPcR*EeKPopqjsPm6NlnEKYFypRCPwABJwI2fxKV7.heiKzOZgzxlGcllyCWk!/r',name:'星际穿越',time:'剧情 / 科幻 / 冒险',path:'508'},
                    {url:'http://r.photo.store.qq.com/psc?/V143D3j445iBwL/ubiEST8aMMlZjEEUGVmWIsHlq8B4pEPEhWB6KoTBHeLsgMwfxRf.9w1YGOoijySbNVwQBKmCA9WtjDXuhj8rRrDd.Bo4ZYlNiQ2G.b*txrs!/r',name:'红海行动',time:'动作 / 战争',path:'509'},
                ],
                urid:'',
                activeIndex: '1',
                activeIndex2: '1',
                input: '',
                currentDate: new Date(),
                isCollapse: false,

                search: "", //当前输入框的值
                isFocus: false, //是否聚焦
                isLogin: true,
                hotSearchList: ["暂无热门搜索"], //热门搜索数据
                historySearchList: [], //历史搜索数据
                searchList: ["暂无数据"], //搜索返回数据,
                history: false,
                types: ["", "success", "info", "warning", "danger"] //搜索历史tag式样
            };
        },
        methods: {
            handleSelect(key, keyPath) {
                console.log(key, keyPath);
            },
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            cardClick(Keypath) {
                this.$router.push({path: Keypath})
            },

            focus() {
                this.isFocus = true;
                this.historySearchList =
                    Store.loadHistory() == null ? [] : Store.loadHistory();
                this.history = this.historySearchList.length == 0 ? false : true;
            },
            blur() {
                var self = this;
                this.searchBoxTimeout = setTimeout(function() {
                    self.isFocus = false;
                }, 300);
            },
            enterSearchBoxHanlder() {
                clearTimeout(this.searchBoxTimeout);
            },
            searchHandler() {
                //随机生成搜索历史tag式样
                let n = RandomUtil.getRandomNumber(0, 5);
                let exist =
                    this.historySearchList.filter(value => {
                        return value.name == this.search;
                    }).length == 0
                        ? false
                        : true;
                if (!exist) {
                    this.historySearchList.push({ name: this.search, type: this.types[n] });
                    Store.saveHistory(this.historySearchList);
                }
                this.history = this.historySearchList.length == 0 ? false : true;
            },
            closeHandler(search) {
                this.historySearchList.splice(this.historySearchList.indexOf(search), 1);
                Store.saveHistory(this.historySearchList);
                clearTimeout(this.searchBoxTimeout);
                if (this.historySearchList.length == 0) {
                    this.history = false;
                }
            },
            removeAllHistory() {
                Store.removeAllHistory();
            }

        },
        props:{
            msg: String
        },
        computed: {
            isHistorySearch() {
                return this.isFocus && !this.search;
            },
            isSearchList() {
                return this.isFocus && this.search;
            },
            isSearch() {
                return this.isFocus;
            }
        },
        created() {
            var guro=localStorage.getItem('username');
            var gurt=sessionStorage.getItem('username');
            if(guro!=null) this.urid=guro;
            if(gurt!=null) this.urid=gurt;
            console.log(guro);
            console.log(gurt);
            console.log(this.urid);
        }
    }
</script>
<style scoped>
    html,
    body,
    .homeBox,
    .el-main,
    .el-header,
    .el-container{
        margin:0;
        padding: 0;
        height: 100%;
        width: 100%;
    }

    .el-aside{
        background-color: #475669;
    }

    .el-main .el-row{
        padding-right: 30px;
    }

    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 200px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }

    .time {
        font-size: 13px;
        color: #999;
    }

    .bottom {
        margin-top: 13px;
        line-height: 12px;
    }

    .button {
        padding: 0;
        float: right;
    }

    .image {
        width: 100%;
        display: block;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 400px;
    }

    .left {
        margin-left: 200px;
    }
    .center {
        margin-top: 15px;
        margin-left: 200px;
    }
    #search {
        background-color: cornflowerblue;
        border-radius: 0%;
    }
    .search-title {
        color: #bdbaba;
        font-size: 15px;
        margin-bottom: 5px;
    }
    .remove-history {
        color: #bdbaba;
        font-size: 15px;
        float: right;
        margin-top: -22px;
    }
    #search-box {
        width: 555px;
        height: 300px;
        margin-top: 0px;
        padding-bottom: 20px;
    }
</style>
